import Vue from 'vue'
import VueRouter from 'vue-router'
import Find from 'views/find/Find'
import Home from 'views/home/Home'

Vue.use(VueRouter)

const routes = [
  {
    path:"/",
    redirect:'/find'
  },
  {
    path:"/home",
    component:Home
  },
  {
    path:"/find",
    name:"Find",
    component:Find
  },
]

const router = new VueRouter({
  
  // mode:'history',
  routes,}
  
)
export default router